.message {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.imageAndText { 
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    align-items: center;
    width: 50%;
}

.imageAndText img {
    height: 48px;
}

.text {
    padding: 7px 25px 0 25px;
    color: white;
    width: 100%;
    min-height: 60px;
    background-image: url('./bg.png');
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.text:after {

}

.name {
    margin-top: 7px;
    font-family: 'Montserrat';
    font-size: 18px;
    font-weight: 600;
    line-height: 21.94px;
    text-align: left;
}

